.div-storematch {
    position: fixed;
    right: 5px;
    bottom: 5px;
    z-index: 999;
    position: fixed;
}
.div-storematch:hover {
    .text-hover-storematch{
        display: block;
    }
}
.text-hover-storematch{
    display: none;
}
.div-storematch-banner {
    text-align: center;
    background-color: #00a1e029;
    /* color: white; */
    padding: 20px 0px 20px 0px;
    margin: 0px 0px 40px 0px;
}
.modal-body-storematch {
    padding: 0;
    height: 100vh;
    width: 100vw;
}
.modal-content-storematch {
    width: 100vw;
}
button.close-modal-button.close-button-storematch {
    position: absolute;
    right: 35px;
    top: 30px;
    border-radius: 30px;
    border: none;
    width: 50px;
    height: 50px;
    font-size: 30px;
    background-color: #ffffff59;
}
button.close-modal-button.close-button-storematch span {
    vertical-align: super;
}
.div-storematch-banner {
    background: url(https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fstatic.vecteezy.com%2Fsystem%2Fresources%2Fpreviews%2F000%2F598%2F445%2Foriginal%2Flight-pastel-color-vector-low-poly-crystal-background-polygon-design-pattern-low-poly-illustration-background.jpg&f=1&nofb=1);
    background-size: cover;
    font-family: monospace;
}